.backgroundImage {
  height: 100vh;
  width: 100%;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  /* color: #c7242e; */
  color: white;
  padding: 10rem;
  justify-content: center;
}
.backgroundImage span {
  font-size: 6rem;
  font-family: "Times New Roman", Times, serif !important;
}
.backgroundImage p {
  font-size: 3rem !important;
  padding-top: 1rem;
  font-family: "Times New Roman", Times, serif !important;
}
.backgroundImage button {
  background-color: #a7232c;
  border: none;
  border-radius: 4px;
  color: #fff;
  display: block;
  font-family: inherit;
  font-size: 16px;
  padding: 1rem 10px;
  margin-top: 20px;
  width: 90px;
  display: inline;
}
.backgroundImage button:hover {
  background-color: #581217;
}
.backgroundImage a {
  width: 5%;
}


@media (max-width: 561px) {
  .backgroundImage span {
    font-size: 4rem;
  }
  .backgroundImage {
    padding: 0px 2rem !important;
  }
}
