  align-items: center;
}
.profile .text {
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.4);
  width: 60%;
  margin: 6rem;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profile h1 {
  font-family: "Times New Roman", Times, serif !important;
  font-size: 5rem;
  color: #a7232c;
}
.profile-hr {
  width: 100% !important;
}
.profile p {
  font-family: "Times New Roman", Times, serif !important;
  font-size: 1.7rem;
  padding-top: 1rem;
  color: gray;
}
.personal-info {
  margin-top: 3rem;
  color: gray;
}
.personal-info h2 {
  margin-bottom: 3rem;
}
.personal-info label {
  font-size: 1.2rem;
  margin-top: 1.5rem;
}

.profile .personal-info input {
  color: gray;
  font-size: 1.5rem;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 45px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #fff;
  border: 1px solid #dcdcdc;
  border-radius: 0;
  font-family: mango-light, Arial;
  font-size: 12px;
  color: #000;
  letter-spacing: 0.3px;
  margin-top: 0.5rem;
}
.container h2 {
  color: gray;
  font-size: 3rem;
  font-family: "Times New Roman", Times, serif;
}
.added-items {
  display: flex;
  margin-top: 5rem;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}
.added-items .name_ {
  font-size: 1.8rem;
  font-family: "Times New Roman", Times, serif;
  color: gray;
  margin-bottom: 3rem;
  margin-top: 0.5rem;
  text-align: center;
}
.purchase {
  text-align: center;
}

.added-items .name_ span {
  font-family: "Times New Roman", Times, serif;
  font-size: 1.4rem;
  font-weight: bolder;
}
.added-items img {
  width: 22rem;
  height: 25rem;
  padding: 2rem;
  border-radius: 3rem;
}
.weather-section {
  display: flex;
  align-items: center !important;
  justify-content: center;
  flex-direction: column;
  border-radius: 1rem;
}
.weather-section .card {
  display: flex;
  align-items: center !important;
  justify-content: center !important;
  border-radius: 3rem;
  margin-right: 2rem !important;
  background-color: #eaeaea;
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.2);
}

.weather-section .card span {
  font-size: 4rem;
}

@media screen and (max-width: 820px) {
  .profile {
    flex-direction: column-reverse !important;
  }
  .profile .text {
    width: 90% !important;
  }
  .weather-section,
  .weather-section .card {
    margin: 3rem;
    width: 95% !important;
  }
}
@media screen and (max-width: 450px){
  .profile {
    padding: 0;
  }
}
